body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-size: 10px;
}
h1 {
  font-size: 1.4rem;
  font-weight: 400;
}
h2 {
  font-size: 1.2rem;
  font-weight: 400;
}
h3 {
  font-size: 1rem;
  font-weight: 300;
}
p {
  font-size: 0.8rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Media queries to implement responsiveness on different screen sizes */
/* Small devices (landscape phones, 576px and up) */
@media screen and (min-width: 576px) {
  h1 {
    font-size: 1.4rem;
    font-weight: 400;
  }
  h2 {
    font-size: 1.2rem;
    font-weight: 400;
  }
  h3 {
    font-size: 1rem;
    font-weight: 300;
  }
}

/* Medium devices (tablets, 768px and up) */
@media screen and (min-width: 768px) {
  h1 {
    font-size: 1.6rem;
    font-weight: 400;
  }
  h2 {
    font-size: 1.4rem;
    font-weight: 400;
  }
  h3 {
    font-size: 1.2rem;
    font-weight: 300;
  }
}

/* Large devices (desktops, 992px and up) */
@media screen and (min-width: 992px) {
  h1 {
    font-size: 1.6rem;
    font-weight: 400;
  }
  h2 {
    font-size: 1.4rem;
    font-weight: 400;
  }
  h3 {
    font-size: 1.2rem;
    font-weight: 300;
  }
}

/* Extra large devices (large desktops, 1200px and up)*/
@media screen and (min-width: 1200px) {
  body {
    font-size: 16px;
  }
  h1 {
    font-size: 1.6rem;
    font-weight: 400;
  }
  h2 {
    font-size: 1.4rem;
    font-weight: 400;
  }
  h3 {
    font-size: 1.2rem;
    font-weight: 300;
  }
}

.App {
  display: block;
  flex-flow: column;
  height: 100vh;
  width: 100%;
}

main {
  display: flex;
  flex-flow: row;
  height: calc(100vh - 4rem);
  width: 100%;
}

.footer {
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  margin: 0.5rem auto;
  justify-content: center;
}

@media screen and (max-width: 576px) {
  .main {
    height: calc(100vh - 3rem);
  }
}

.error-page {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.error {
  display: block;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  background-color: purple;
  color: white;
  box-shadow: 1px 1px 15px 5px purple;
  padding: 1.5rem;
}

@media screen and (max-width: 768px) {
  .error-page {
    display: block;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }
  .error {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 2rem auto 0 auto;
  }
}

.header {
  display: flex;
  align-items: center;
  height: 4rem;
  width: 100%;
  padding: 0rem;
  background-color: purple;
  text-align: center;
}

.title {
  justify-content: center;
  flex-grow: 1;
}

.header h1 {
  font-weight: 500;
  color: white;
}

@media screen and (max-width: 576px) {
  .header {
    height: 3rem;
  }
}

