.hamburger-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
span.hamburger-inner,
span.hamburger-inner::after,
span.hamburger-inner::before {
  background-color: white;
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::after,
.hamburger.is-active .hamburger-inner::before {
  background-color: white;
}

.map-container {
  display: block;
  height: 100%;
  width: 100%;
}

.loading-element {
  height: 100%;
}

.map-element {
  height: 100%;
}
li {
  list-style: none;
  margin-top: 1rem;
  cursor: pointer;
}

.list-item {
  display: flex;
  flex-flow: row;
  padding: 0rem 0.5rem 0rem 0.5rem;
  justify-content: left;
  align-items: center;
  background-color: purple;
  color: white;
  border-radius: 10px;
  box-shadow: 1px 1px 10px 3px purple;
}

.list-item:focus,
.list-item:hover {
  background-color: #be01be;
}

.list-item img {
  display: inline;
  margin-right: 0.5rem;
}

.list-item p {
  display: inline;
  font-weight: 500;
  font-size: 1rem;
}

@media screen and (max-width: 576px) {
  .list-item p {
    font-size: 0.7rem;
  }
  .list-item img {
    display: none;
  }
}

.venue-list {
  list-style-type: none;
  padding: 0rem 1rem 0rem 1rem;
  margin-top: 0rem;
}

.sidebar-container {
  display: block;
  height: 100%;
  width: 40%;
  float: left;
}

.sidebar-menu {
  display: flex;
  flex-flow: column;
  height: 100%;
  width: 100%;
  overflow: scroll;
}

.search-box {
  display: inline-block;
  padding: 1rem;
  margin-top: 1rem;
}

#search {
  width: 100%;
  border: none;
  border-bottom: 2px solid purple;
  padding: 0.5rem;
  font-size: 1rem;
}

.attribution {
  display: block;
  padding: 1rem 1.5rem 1rem 1.5rem;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .sidebar-container {
    width: 80%;
  }
}

